@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro");
.Search,
.TypePr,
.Volume {
  padding: 15px;
  background-color: #edf0f7;
  border-radius:15px;

}
.Type{
  background-image: url(../../ProdImg/Background/BackPr.jpg);
  /* backface-visibility: visible; */

}
.Type div > div > div> div > div > div > div > div > div> div > svg > path{
  color: black;
}
.Type svg text {
  font-family: Lora;
  letter-spacing: 7px;
  stroke: #0f0be6d5;
  font-size: 100px;
  font-weight: 700;
  stroke-width: 2;
 
  animation: textAnimate 5s infinite alternate;
  
}
@keyframes textAnimate {
  0% {
    stroke-dasharray: 0 50%;
    stroke-dashoffset:  20%;
    fill:hsl(0, 100%, 1%)

  }
  
  100% {
    stroke-dasharray: 50% 0;
    stroke-dashoffstet: -20%;
    fill: rgba(0, 0, 0, 0.199)
  }
  
}
.Type .RefPr{
 
  font-style: italic;
  background: -webkit-linear-gradient(rgba(17, 53, 214, 0.555), rgb(26, 0, 155));
  -webkit-background-clip: text;  
  -webkit-text-fill-color: transparent;
}
.Type .volume,.matierePr,.Poids,.SacUnites{
  margin-left:10px;
  font-size:14px;

}
.Type .volume >span,.Type .matierePr >span,.Type  .Poids>span,.Type .SacUnites>span{
  font-style: oblique!important;
  font-size:15px;
  font-weight:600;
  color:rgb(0, 0, 0);
  /* font-style: italic; */
  margin-right: 6px;
  width: 100px!important;
}
.Type span {
  color: #99a7cd;
  font-style: oblique!important;

}




.Type .filter {
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
  
}



.Type .product-details-wrapper {
    position: relative;
    min-height: 200px;
    font-size: 12px;
    letter-spacing: inherit !important;
}
.Type .product-title {
    display: block;
    font-size: 20px;
}
.Type .fusion-product-content {
  padding: 20px 15px 15px 15px;
}

.Type .images-Products > div > div > div.image-gallery-slide-wrapper.bottom > div > div > div.image-gallery-slide.center > img{
  position: relative !important;
  border-radius:16px;
  background-size: cover;
